:root {
  --primary-blue: #1E3A8A;
  --primary-yellow: #FBBF24;
  --secondary-blue: #3B82F6;
  --light-yellow: #FEF3C7;
  --dark-blue: #1E40AF;
}

/* Базовые стили */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #333;
  background-color: #fff;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Шапка */
.header {
  background-color: var(--primary-blue);
  color: white;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.logo-container {
  display: flex;
  flex-direction: column;
}

.logo {
  font-size: 2rem;
  margin: 0;
  color: var(--primary-yellow);
  font-weight: 700;
}

.logo-sub {
  font-size: 0.9rem;
  opacity: 0.9;
  margin: 0;
}

.nav {
  display: flex;
  gap: 2rem;
}

.nav a {
  color: white;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.nav a:hover {
  color: var(--primary-yellow);
}

.menu-button {
  display: none;
  background: none;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
}

/* Основные секции */
section {
  padding: 4rem 2rem;
}

.section-title {
  text-align: center;
  color: var(--primary-blue);
  margin-bottom: 3rem;
  font-size: 2rem;
}

/* Герой секция */
.hero-section {
  background: linear-gradient(135deg, var(--primary-blue) 0%, var(--secondary-blue) 100%);
  color: white;
  padding: 6rem 2rem;
  text-align: center;
}

.hero-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero-content p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto 2rem;
}

/* Преподаватели */
.teachers-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.teacher-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.teacher-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0.1);
}

.teacher-main {
  display: flex;
  align-items: center;
  padding: 1.5rem;
  cursor: pointer;
  position: relative;
}

.teacher-main img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 1.5rem;
  border: 3px solid var(--primary-yellow);
}

.teacher-info {
  flex: 1;
}

.teacher-info h3 {
  margin: 0;
  color: var(--primary-blue);
}

.teacher-info .subject {
  color: var(--secondary-blue);
  margin: 0.5rem 0 0;
}

.arrow {
  color: var(--primary-yellow);
  font-size: 1.2rem;
  transition: transform 0.3s;
}

.arrow.down {
  transform: rotate(0deg);
}

.arrow.up {
  transform: rotate(180deg);
}

.teacher-details {
  padding: 0 1.5rem 1.5rem;
  background-color: var(--light-yellow);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

/* Курсы */
.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.course-card {
  background: white;
  border-radius: 10px;
  padding: 2rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-top: 4px solid var(--primary-yellow);
  text-align: center;
}

.course-card h3 {
  color: var(--primary-blue);
  margin-top: 0;
}

/* Кнопки */
.button {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s;
  border: none;
  cursor: pointer;
}

.cta-button {
  background-color: var(--primary-yellow);
  color: var(--primary-blue);
  font-size: 1.1rem;
  padding: 1rem 2rem;
}

.cta-button:hover {
  background-color: #F59E0B;
  transform: translateY(-2px);
}

.teacher-button {
  background-color: var(--primary-blue);
  color: white;
  width: 100%;
  margin-top: 1rem;
}

.teacher-button:hover {
  background-color: var(--dark-blue);
}

.course-button {
  background-color: white;
  color: var(--primary-blue);
  border: 2px solid var(--primary-blue);
  margin-top: 1rem;
}

.course-button:hover {
  background-color: var(--primary-blue);
  color: white;
}

/* Подвал */
.footer {
  background-color: var(--primary-blue);
  color: white;
  padding: 2rem 0;
  margin-top: auto;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.social-links {
  display: flex;
  gap: 1.5rem;
}

.social-links a {
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}

.social-links a:hover {
  color: var(--primary-yellow);
}

/* Адаптивность */
@media (max-width: 768px) {
  .nav {
    display: none;
    position: absolute;
    top: 70px;
    left: 0;
    right: 0;
    background-color: var(--primary-blue);
    flex-direction: column;
    padding: 1rem;
    gap: 1rem;
  }
  
  .nav.open {
    display: flex;
  }
  
  .menu-button {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .menu-button span {
    display: block;
    width: 25px;
    height: 3px;
    background-color: white;
    transition: all 0.3s;
  }
  
  .menu-button.open span:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
  }
  
  .menu-button.open span:nth-child(2) {
    opacity: 0;
  }
  
  .menu-button.open span:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
  }
  
  .hero-content h2 {
    font-size: 2rem;
  }
  
  .teacher-main {
    padding: 1rem;
  }
  
  .teacher-main img {
    width: 60px;
    height: 60px;
    margin-right: 1rem;
  }
}
